	module.exports = {"data":{"markdownRemark":{"html":"<p>It’s been a really long time since I blogged and as I’ve said before, I really think it’s a valuable outlet and method of reflection. So, in an effort to begin again: here’s what I’m working on right now.</p>\n<h2>The Job Front</h2>\n<p>As many of you may know, I resigned from my job in February.</p>\n<pre><code>(ﾉ°O°)ﾉ\n</code></pre>\n<p>And went full freelance. It was a terrifying leap, but I’ve been talking about it for a really long time and it seemed like as good a time as any to just try it and see what happens. So far, I’ve had a lot of work, thanks to a great network, but I’ve found myself working 24/7 (which is pretty typical). One lesson I’m still learning is how to be more choosy about the work I want to do in the world. On the one hand, of course, I’m grateful for every opportunity to have work, but on the other hand, it’s become quite a lot and I’m having to skip on really, really cool opportunities because I’m trying to get work done. And that stinks.</p>\n<h2>Game Development</h2>\n<p>Work, as I’ve said, has been really kicking my butt, so I haven’t had a ton of time to continue my game development learning (meanwhile, my list of things to learn is growing longer). But, I’ve had a game that I’ve had on whole for a least a month or so and, this weekend, I decided to buckle down and make time to get it into a finished state. It’s not perfect, but I’m proud of it!</p>\n<figure>\n  <a href=\"https://anthkris.itch.io/Campaign\" target=\"blank\">\n    \n  <span class=\"gatsby-resp-image-wrapper\" style=\"position: relative; display: block; ; max-width: 810px; margin-left: auto; margin-right: auto;\">\n    <span class=\"gatsby-resp-image-background-image\" style=\"padding-bottom: 55.6469708302169%; position: relative; bottom: 0; left: 0; background-image: url(&apos;data:image/png;base64,&apos;); background-size: cover; display: block;\">\n      <img class=\"gatsby-resp-image-image\" style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\" alt=\"game screenshot\" title=\"\" src=\"/static/Screen-Shot-2017-04-16-at-3.46.47-PM-20f7c4765939f6ef86eb32ef7681485c-1626e.png\" srcset=\"/static/Screen-Shot-2017-04-16-at-3.46.47-PM-20f7c4765939f6ef86eb32ef7681485c-076ec.png 203w,\n/static/Screen-Shot-2017-04-16-at-3.46.47-PM-20f7c4765939f6ef86eb32ef7681485c-7ae34.png 405w,\n/static/Screen-Shot-2017-04-16-at-3.46.47-PM-20f7c4765939f6ef86eb32ef7681485c-1626e.png 810w,\n/static/Screen-Shot-2017-04-16-at-3.46.47-PM-20f7c4765939f6ef86eb32ef7681485c-9af38.png 1215w,\n/static/Screen-Shot-2017-04-16-at-3.46.47-PM-20f7c4765939f6ef86eb32ef7681485c-03d88.png 1620w,\n/static/Screen-Shot-2017-04-16-at-3.46.47-PM-20f7c4765939f6ef86eb32ef7681485c-ca085.png 2430w,\n/static/Screen-Shot-2017-04-16-at-3.46.47-PM-20f7c4765939f6ef86eb32ef7681485c-e4875.png 2674w\" sizes=\"(max-width: 810px) 100vw, 810px\">\n    </span>\n  </span>\n  \n    <figcaption>Play the game!</figcaption>\n  </a>\n</figure>\n<p>I’m also working on an iteration 1.0 of an xAPI-enabled game with <a href=\"https://twitter.com/oxala75\">Craig Wiggins</a> for the current xAPI cohort. The game has definitely come a long way, but I’m still worried that it’s not a very good game. Still, it’s been really interesting thinking through data collection and game design with co-designer!</p>\n<figure>\n  <a href=\"http://www.knanthony.com/showcase/budget-game/BudgetStory2.html\" target=\"blank\">\n    \n  <span class=\"gatsby-resp-image-wrapper\" style=\"position: relative; display: block; ; max-width: 810px; margin-left: auto; margin-right: auto;\">\n    <span class=\"gatsby-resp-image-background-image\" style=\"padding-bottom: 54.66573816155989%; position: relative; bottom: 0; left: 0; background-image: url(&apos;data:image/png;base64,&apos;); background-size: cover; display: block;\">\n      <img class=\"gatsby-resp-image-image\" style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\" alt=\"game screenshot for twine game\" title=\"\" src=\"/static/screencapture-file-Users-anthkris-Documents-City-20Budget-20Game-BudgetStory2-html-1492385379385-504850557ca3c5db4fc70377afa29a80-1626e.png\" srcset=\"/static/screencapture-file-Users-anthkris-Documents-City-20Budget-20Game-BudgetStory2-html-1492385379385-504850557ca3c5db4fc70377afa29a80-076ec.png 203w,\n/static/screencapture-file-Users-anthkris-Documents-City-20Budget-20Game-BudgetStory2-html-1492385379385-504850557ca3c5db4fc70377afa29a80-7ae34.png 405w,\n/static/screencapture-file-Users-anthkris-Documents-City-20Budget-20Game-BudgetStory2-html-1492385379385-504850557ca3c5db4fc70377afa29a80-1626e.png 810w,\n/static/screencapture-file-Users-anthkris-Documents-City-20Budget-20Game-BudgetStory2-html-1492385379385-504850557ca3c5db4fc70377afa29a80-9af38.png 1215w,\n/static/screencapture-file-Users-anthkris-Documents-City-20Budget-20Game-BudgetStory2-html-1492385379385-504850557ca3c5db4fc70377afa29a80-03d88.png 1620w,\n/static/screencapture-file-Users-anthkris-Documents-City-20Budget-20Game-BudgetStory2-html-1492385379385-504850557ca3c5db4fc70377afa29a80-ca085.png 2430w,\n/static/screencapture-file-Users-anthkris-Documents-City-20Budget-20Game-BudgetStory2-html-1492385379385-504850557ca3c5db4fc70377afa29a80-1113d.png 2872w\" sizes=\"(max-width: 810px) 100vw, 810px\">\n    </span>\n  </span>\n  \n    <figcaption>Playtesters needed!</figcaption>\n  </a>\n</figure>\n<h2>JavaScript</h2>\n<p>My general JavaScript learning has also been put on hold for a bit. I haven’t been doing much with Oppia lately, at least as far as actually creating things. And I haven’t been digging into the coding projects on my list, though I have gotten in a bit of coding with my Campaign game and the City Budget game (the two games mentioned above). I’m hoping to change all that this summer. One exciting (and maddening) opportunity I currently have is to help build a web app for one of my clients. So far it’s been a lot of UI/UX work for which I feel woefully under-qualified and now we’re trying to prototype and iterate in the browser. The snag is that we’re using Django, which is Python (not something I’ve studied) as well as a whole host of other tech that I have no experience with, such as Docker (I have no idea what I’m doing). So there’s a ton to learn and it’s been really frustrating, especially because I’m juggling so many other things. I expect things will get easier, but right now, I’m decidedly in the stinking really bad at something stage. I’ve also been doing some Canvas development which has included using JavaScript (and a lot of CSS) to make Canvas pages responsive and generally more contemporary-looking.</p>\n<h2>Reading</h2>\n<p>I finally finished Thinking Fast and Slow on a long trip. That is a huge book but it’s really really good and I expect that I’ll continue to return to it’s concepts. I also completed Rework really quickly on the drive back (not as good, in my opinion, but much, much shorter) I was trying to start SuperBetter and Algorithms to Live By, but I’m finding myself really not into either one right now and everything I’m reading on how to read more suggests being willing to give up on books you aren’t into. So, for now at least, I’m moving on to Weapons of Math Destruction.</p>\n<h2>Moving Forward</h2>\n<p>There’s more work in my immediate future, but I’m serious about trying to slow down this summer and refocus on my game dev and coding learning.</p>","frontmatter":{"date":"April 18, 2017","path":"/blog/weeknotes-418-in-which-i-finally-finish-my-game","title":"Weeknotes 4/18: A Brief Recap of what I've been doing since I blew up my life","tags":["business","coding","freelancing","game dev"],"categories":["Coding","GameDev","Weeknotes"],"featured":{"childImageSharp":{"sizes":{"base64":"data:image/png;base64,","aspectRatio":2,"src":"/static/WeekNotes-42F18-46f16b2d17cf732e6255b70cf7da8d42-81d39.png","srcSet":"/static/WeekNotes-42F18-46f16b2d17cf732e6255b70cf7da8d42-9a8d1.png 203w,\n/static/WeekNotes-42F18-46f16b2d17cf732e6255b70cf7da8d42-c53f9.png 405w,\n/static/WeekNotes-42F18-46f16b2d17cf732e6255b70cf7da8d42-81d39.png 810w,\n/static/WeekNotes-42F18-46f16b2d17cf732e6255b70cf7da8d42-fda19.png 1024w","sizes":"(max-width: 810px) 100vw, 810px"}}}}}},"pathContext":{}}

